body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-size: 16px !important; */
  /* visibility: hidden !important; */
}

ul {
  list-style: none; /* Remove list bullets */
}

a {
/*   color: var(--secondary-color-dark) !important; */
  font-weight: bold;
  font-family: "Open Sans Condensed" !important;
  font-family: var(--secondary-font-family) !important;
  font-size: 1.1rem !important;
}
a:hover {/* color: var(--contrast-color) !important; */}


/* --secondary-color: #A0EF83;
--primary-color: #77D9C3; */

:root {
  --secondary-color: #11998e;
  --secondary-color-light: #A7EDDD; /* lighten(var(--secondary-color), 50%); */
  --secondary-color-dark: #00635C; /* #4FBFA6; *//*  darken(var(--secondary-color), 50%); */

  --primary-color: #38ef7d; 
  --primary-color-light: #C2F8AE; /* lighten(var(--primary-color), 50%); */
  --primary-color-dark: #83E55F; /* darken(var(--primary-color), 50%); */
  
  --primary-white: #DCFBF4;
  --contrast-color: #FFB88C;
  --contrast-color-light: #FFEBDF;
  --primary-font-family: "Open Sans";
  --secondary-font-family: "Open Sans Condensed";
}

